burger.click(function (){
  burger.toggleClass("burger-click");
  bBox.toggleClass("burger-box-click");
  $(".opacity-block").toggleClass("display-none");
  $(".top-menu").toggleClass(function(){
    $(document).mouseup(function (e) {
      if(burger.hasClass('burger-click')){
        if ((menu.has(e.target).length === 0) && (bBox.has(e.target).length === 0)) {
          burger.removeClass('burger-click'); 
          bBox.removeClass("burger-box-click");         
          $(".opacity-block").addClass("display-none");
          $(".top-menu").removeClass("top-menu-active");
        };
      }; 
    });
    return "top-menu-active";   
  });
});

 

