function showHover(langBtn,langHoverbox){
	$(langBtn).mouseover(function (e) {
		$(langBtn).addClass("index-z-4");
    $(langHoverbox).addClass("index-z-4");
		$(langHoverbox).removeClass("display-none");
		$(".opacity-block").removeClass("display-none");
		jQuery(function($){
			$(langBtn).mouseout(function (e){
				$(langHoverbox).addClass("display-none");
				$(".opacity-block").addClass("display-none");
				$(langBtn).removeClass("index-z-4")
			});
		});
	});
}

function showClick(langBtn,langHoverbox){
  $(langBtn).click(function (e) {
    $(langBtn).toggleClass("index-z-4");
    $(langHoverbox).toggleClass("index-z-4");
    $(langHoverbox).toggleClass("display-none");
    $(".opacity-block").toggleClass("display-none");
    jQuery(function($){
      $(".opacity-block").click(function (e){
        $(langHoverbox).addClass("display-none");
        $(".opacity-block").addClass("display-none");
        $(langBtn).removeClass("index-z-4");
        $(langHoverbox).removeClass("index-z-4");
      });
    });
  });
};